import React, { useContext, useEffect, useState } from "react";
import Modal from "react-modal";
import { GrFormClose } from "react-icons/gr";
import { GrClose } from "react-icons/gr";
import { HiCurrencyRupee } from "react-icons/hi";
import { FiUpload } from 'react-icons/fi';
import axios from "axios";
import { HEADER } from "../../Trade/tradeComponent/TradeApiListFile";
import WaterApiList from "../../../Components/ApiList/WaterApiList";
import { contextVar } from "../../../Components/ContextVar";
import AxiosInterceptors from "../../../Components/GlobalData/AxiosInterceptors";
import uploadImg from "./upload.gif"

const customStyles = {
    content: {
        top: "50%",
        left: "50%",
        right: "auto",
        bottom: "auto",
        marginRight: "-50%",
        transform: "translate(-50%, -50%)",
        background: "transparent",
        border: "none",
    },
};

function WaterDocumentModalForOwner(props) {
    const [errorMessage, setErrorMessage] = useState()
    // Modal.setAppElement('#yourAppElement');
    const [modalIsOpen, setIsOpen] = React.useState(false);
    const [docType, setDocType] = useState()
    const [ownerId, setOwnerId] = useState()
    const [docPath, setDocPath] = useState()
    const [isUploading, setIsUploading] = useState(false)


    const { notify } = useContext(contextVar)

    const { api_getDocList, api_waterUploadDoc, header } = WaterApiList();

    const data = props?.payloadData;

    useEffect(() => {
        if (props.openDocumentModal > 0) setIsOpen(true);
    }, [props.openDocumentModal]);

    function afterOpenModal() { }

    function closeModal() {
        setIsOpen(false);
        setDocType()
        setDocPath()
        // props.refetchListOfRoles();
    }

    console.log("Props in water doc upload", props)


    const docUpload = (e) => {
        e.preventDefault();

        if (docType == undefined || docType == "" || docPath == undefined || docPath == "") {
            return setErrorMessage("Please Select Correct File and Type")
        }

        //Check Extension
        const allowedExtensions = ['jpg', 'jpeg', 'png', 'pdf'];
        const fileExtension = docPath?.name?.split('.').pop().toLowerCase();
        if (!allowedExtensions.includes(fileExtension)) {
            setErrorMessage("Invalid file extension")
            return;
        }

        let formData = new FormData();

        formData.append('applicationId', props?.payloadData?.applicationId);
        formData.append('docCategory', props?.payloadData?.docCategory);
        formData.append('document', docPath);  //doucment master id
        formData.append('docCode', docType);            //Btn name
        // formData.append('docMstrId', docType);            //Btn name
        // formData.append('docRefName', props?.payloadData?.docName);            //File information
        formData.append('ownerId', props?.payloadData?.ownerId);   //ownerID


        console.log("Owner Document Complate Payload", formData);
        setIsUploading(true)
        AxiosInterceptors.post(api_waterUploadDoc, formData, header)
            .then((res) => {
                // console.log("Data Uploaded", res)
                // closeModal()
                // props.refetch()
                if (res.data.status == true) {
                    notify('Document Uploaded Successfully', 'success')
                    console.log("Data Uploaded", res)
                    setIsUploading(false)
                    closeModal()
                    props.refetch()
                } else {
                    setIsUploading(false)
                    notify('Failed to Upload Document', 'error')
                    setErrorMessage("Error : Failed to upload file. Please Select Proper File.")
                }
            })
            .catch((err) => {
                setIsUploading(false)
                notify('Something went wrong', 'error')
                setErrorMessage("Exception : Failed to upload file. Please Select Proper File.")
                console.log("Expection...", err)
            })


        // setFileSelected(e.target.files[0]);
    };


    // useEffect(() => {
    //     axios.post(api_getDocList, { "applicationId": props?.payloadData?.applicationId }, HEADER())
    //         .then((res) => {
    //             console.log("Llist of document to upload", res)
    //             if (res.data.status == true) {
    //                 // setDocumentList(res.data.data.documentsList)
    //                 setOwnerDocList(res.data.data.ownersDocList)
    //             }
    //         })
    //         .catch((err) => console.log("Error in Document upload list", err))
    // }, [props?.payloadData?.applicationId])


    console.log("Paylidadd data in owner details", data)

    return (
        <div className="">
            <Modal
                isOpen={modalIsOpen}
                onAfterOpen={afterOpenModal}
                onRequestClose={closeModal}
                style={customStyles}
                contentLabel="Example Modal"
            >
                <form encType="multipart/form" onSubmit={docUpload} >
                    <div className="bg-indigo-200 shadow-2xl border border-sky-200 p-2 rounded-md">
                        <div className="my-2 mx-3">
                            <div onClick={closeModal} className="float-right cursor-pointer hover:bg-gray-300 rounded-sm p-1"> <GrClose /></div>
                            <h1 className="font-semibold md:text-xl text-sm text-gray-700">Upload Document
                                {/* {props?.payloadData?.docName?.replace(/_/g, ' ')} */}
                            </h1>
                        </div>

                        <div className="md:inline-block min-w-full overflow-hidden hidden p-4">
                            <table className="min-w-full leading-normal border">
                                <thead className='bg-sky-100'>
                                    <tr className='font-semibold '>
                                        <th scope="col" className="px-5 py-2 border-b border-gray-200 text-gray-800  text-left text-sm uppercase">
                                            #
                                        </th>
                                        <th scope="col" className="px-5 py-2 border-b border-gray-200 text-gray-800  text-left text-sm uppercase">
                                            Document Name
                                        </th>
                                        <th scope="col" className="px-5 py-2 border-b border-gray-200 text-gray-800  text-left text-sm uppercase">
                                            Owner Name
                                        </th>
                                        <th scope="col" className="px-5 py-2 border-b border-gray-200 text-gray-800  text-left text-sm uppercase">
                                            Document Type
                                        </th>

                                        <th scope="col" className="px-5 py-2 border-b border-gray-200 text-gray-800  text-left text-sm uppercase">
                                            Upload
                                        </th>
                                        <th scope="col" className="px-5 py-2 border-b border-gray-200 text-gray-800  text-left text-sm uppercase">
                                            Submit
                                        </th>
                                    </tr>
                                </thead>
                                <tbody>
                                    {
                                        // ownerDocList?.filter(e => e.docName == props?.payloadData?.docName).map((e, i = 1) => (
                                        <tr>
                                            <td className="px-5 py-2 border-b border-gray-200 bg-white text-sm">
                                                <p className="text-gray-900 whitespace-no-wrap">
                                                    {1}
                                                </p>
                                            </td>

                                            <td className="px-5 py-2 border-b border-gray-200 bg-white text-sm">
                                                <p className="text-gray-900 whitespace-no-wrap capitalize">
                                                    {/* {e?.docName}{e?.isMadatory && <span className="text-red-500 font-semibold mx-1">*</span>} */}
                                                    {data?.docName?.replace(/_/g, ' ')}
                                                    {/* {typeof(data?.refDocName)} */}

                                                </p>
                                            </td>
                                            <td className="px-5 py-2 border-b border-gray-200 bg-white text-sm">
                                                <p className="text-gray-900 whitespace-no-wrap">
                                                    {data?.ownerName}
                                                </p>
                                            </td>
                                            <td className="px-5 py-2 border-b border-gray-200 bg-white text-sm">
                                                <p className="text-gray-900 whitespace-no-wrap">
                                                    <select onChange={(e) => setDocType(e.target.selectedOptions[0].text)} className=' outline-blue-600 border border-gray-400 w-40'>
                                                        <option>Select Documet</option>
                                                        {
                                                            data?.docVal?.map((item) => (
                                                                <option value={item.id}>{item.doc_name}</option>
                                                            ))
                                                        }
                                                    </select>
                                                </p>
                                            </td>

                                            <td className="px-5 border-b border-gray-200 bg-white text-sm">
                                                <span className="relative inline-block ">
                                                    <span aria-hidden="true" className="absolute inset-0 "></span>
                                                    <span className="relative ">
                                                        <input
                                                            disabled={isUploading}
                                                            name="docPath"
                                                            type="file"
                                                            style={{ display: 'none' }}
                                                            id="contained-button-file"
                                                            onChange={(e) => setDocPath(e.target.files[0])}
                                                        />
                                                        <label className={` ${isUploading ? 'cursor-wait hover:rounded-none' : 'cursor-pointer rounded-sm hover:rounded-md hover:shadow-2xl hover:bg-blue-600 hover:ring-1 hover:ring-blue-800'} bg-blue-500  shadow-lg flex pl-4 pr-5 py-2  text-white font-medium`} htmlFor="contained-button-file"><FiUpload size={16} className='mt-0 ml-0 mr-2 font-medium' />Browse File</label>

                                                    </span>
                                                </span>
                                            </td>
                                            <td className="px-5 py-2 border-b border-gray-200 bg-white text-sm">
                                                {isUploading ? <img className="h-8" src={uploadImg} alt="Uploading" /> :
                                                    <button disabled={isUploading} type="submit" className="border disabled:bg-green-300 disabled:text-gray-500 bg-green-600 rounded-md text-white px-4 py-2">Submit</button>
                                                }
                                                {/* <button type="submit" className="border bg-green-600 rounded-md text-white px-4 py-2">Submit</button> */}
                                                {/* <button onClick={()=>setOwnerId(e?.ownerId)} type="submit" className="border bg-green-600 rounded-md text-white px-4 py-2">Submit</button> */}
                                            </td>
                                        </tr>
                                        // ))
                                    }

                                </tbody>
                            </table>

                            <div className='my-2 flex justify-center font-semibold text-red-600 relative'>
                                <p className="absolute">
                                    {errorMessage && errorMessage}
                                </p>
                            </div>

                        </div>

                        <div className="grid grid-cols-12 md:hidden">
                            <div className="col-span-5 text-sm">
                                <p>Document Name</p>
                                <p>Owner Name</p>
                                <p>Document Type</p>
                                <p>Select File</p>
                            </div>
                            <div className="col-span-7">
                                <p>{data?.docName}</p>
                                <p>{data?.ownerName}</p>
                                <p>
                                    <select onChange={(e) => setDocType(e.target.selectedOptions[0].text)} className=' outline-blue-600 border border-gray-400 h-8 w-32 text-xs'>
                                        <option>Select Document</option>
                                        {
                                            data?.docVal?.map((item) => (
                                                <option value={item.id}>{item.doc_name}</option>
                                            ))
                                        }
                                    </select>
                                </p>
                                <p>
                                    <span className="relative inline-block ">
                                        <span aria-hidden="true" className="absolute inset-0 "></span>
                                        <span className="relative ">
                                            <input
                                                name="docPath"
                                                type="file"
                                                style={{ display: 'none' }}
                                                id="contained-button-file"
                                                onChange={(e) => setDocPath(e.target.files[0])}
                                            />
                                            <label className='bg-blue-500 hover:bg-blue-600 hover:ring-1 hover:ring-blue-800 rounded-sm hover:shadow-2xl shadow-lg cursor-pointer flex pl-2 pr-3 py-1  text-white font-medium' htmlFor="contained-button-file"><FiUpload size={16} className='mt-0 ml-0 mr-2 font-medium' />Browse File</label>
                                        </span>
                                    </span>
                                </p>
                            </div>
                            <div className="text-sm flex m-auto">
                                <button type="button" className="border bg-sky-600 rounded-md text-white px-3 py-1">Close</button>
                                <button disabled={isUploading} type="submit" className="border disabled:bg-green-300 disabled:text-gray-500 bg-green-600 rounded-md text-white px-3 py-1">Submit</button>
                            </div>
                        </div>

                    </div>
                </form>
            </Modal>
        </div>
    );
}

// ReactDOM.render(<App />, appElement);

export default WaterDocumentModalForOwner;
